import React,{Component} from 'react'
import { Row, Col } from 'antd';

class Contact extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        const map = require("biz-imgs/about/map.png");
        return (
            <Row className="contactFrame">
                <Row className="menuTitle">联系方式</Row>
                <Row className="contact">
                    <Row className="line">
                        <Col span={12} className="phone">
                            <Col span={12}>总机电话:  021-20265188</Col>
                            <Col span={12}>客服电话:  4008-333-888</Col>
                        </Col>
                        <Col span={8} offset={4} className="tax">传真:  021-58903578</Col>
                    </Row>
                    <Row className="line">
                        <Col span={12} className="address">地址:  上海市浦东新区浦电路360号陆家嘴投资大厦15楼</Col>
                        <Col span={8} offset={4} className="email">邮编:  200122</Col>
                    </Row>
                    <img src={map} className="map" />
                </Row>
            </Row>
        )
    }
}

module.exports = Contact;


